<template>
  <div class="wrap">
    <app-header title="已创建的账号列表" showBack="true"></app-header>
    <div class="main">
      <div class="item-wrap">
        <div class="item"
           v-for="item in userList"
           :key="item.id">
              <div>
                <h5>{{item.name}} <span>账号: {{item.code}}</span></h5>
                <p><span>权限: {{item.label_name}}</span></p>
                <p><span>创建时间: {{item.create_time}}</span></p>
              </div>
              <div>
                <mt-button type="primary" @click="resetPasswordHandel(item.code)" style="font-size: 1rem;">重置密码</mt-button>
                <mt-button type="danger" @click="removeUser(item.id)" style="font-size: 1rem;">删除</mt-button>
              </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {getDealerSihUserList, removeSihUser, resetSihChildAccountPassword} from '../../api/maintain'
export default {
  name: 'Dealer-Account-List',
  data () {
    return {
      userList: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    getList () {
      getDealerSihUserList()
        .then(data => {
          if (data.state) {
            this.userList = data.data.list
          }
        })
    },
    removeUser (user) {
      this.$messagebox({
        title: '删除该账号',
        message: '确定删除该会员?',
        showCancelButton: true
      })
        .then((data) => {
          if (data === 'confirm') {
            removeSihUser(user)
              .then(data => {
                if (data.state) {
                  this.$toast(data.errormsg || '删除成功')
                  this.getList()
                } else {
                  this.$toast(data.errormsg || '删除失败')
                }
              })
              .catch(err => {
                console.log(err)
                this.$toast('网络错误，请重试')
              })
          }
        })
    },
    resetPasswordHandel (code) {
      this.$messagebox({
        title: '重置密码',
        message: '确认重置该账号的密码？',
        showCancelButton: true
      })
        .then(data => {
          if (data === 'confirm') {
            resetSihChildAccountPassword(code)
              .then(data => {
                if (data.state) {
                  this.$toast(data.errormsg || '重置成功')
                  this.getList()
                } else {
                  this.$toast(data.errormsg || '重置失败')
                }
              })
              .catch(err => {
                console.log(err)
                this.$toast('网络错误，请重试')
              })
          }
        })
    }
  },
  components: {AppHeader}
}
</script>

<style lang="less" scoped>
.wrap {
  height: 100%;
  padding-top: 44px;
  background: #e5e5e5;
  .main {
    margin-bottom: 100px;
    .item-wrap {
      .item {
        display: flex;
        margin-bottom: 1rem;
        align-items: center;
        padding: .5rem 1rem;
        border-bottom: 1px solid #eee;
        background: #fff;
        box-shadow: 0px 2px 16px 3px rgba(0,0,0, .2);
        div {
          flex: 1;
        }
        h5 {
          margin: 0;
          padding: .2rem 0;
          span {
            font-size: 13px;
          }
        }
        p {
          margin: 0;
          padding: .2rem 0;
          font-size: 13px;
          color: #999;
        }
      }
    }
  }
}
</style>



